.App {
  text-align: center;
  min-height: 100vh;
  color: #fff;
  margin-top: 0;
  display: flex;
  flex-direction: column;
}


.info {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: #252930;
  position: fixed;
  bottom: 0;
  z-index: 99;
  width: 100%;
}

.info p:first-child {
  border-radius: 50px 0 0 50px;
}

.info p:last-child {
  border-radius: 0 50px 50px 0;
}

.info p {
  /* border: solid 1px #282c34; */
  /* background: #61dafb; */
  color: #fff;
  width: 150px;
  padding: 3px;
}

.test {
  min-height: 100vh;
  display: flex;
  color: #fff;
  justify-content: center;
  align-items: baseline;
  padding-top: 60px;
  padding-bottom: 70px;
  padding-left: 40px;
  padding-right: 40px;
}

button {
  background-color: #61dafb;
  color: #282c34;
  font-weight: 600;
  border: none;
  height: 40px;
  width: 150px;
  cursor: pointer;
  border-radius: 50px;
  outline: none;
  transition: all 0.3s;
}

button:hover {
  transform: scale(1.2);
}

.text {
  padding: 25px;
  font-size: 1.2em;
  width: 70%;
  margin: auto;
  margin-top: 40px;
}

.v,
.o {
  text-align: center;
  width: 50%;
}

.o p {
  text-align: left;
}

@media screen and (max-width: 900px) {
  .test {
    flex-direction: column;
    justify-content: start;
    padding-left: 0;
    padding-right: 0;
    padding-top: 20px;
  }

  .v,
  .o {
    width: 100%;
  }

  .text {
    padding-left: 0;
    padding-right: 0;
    width: 85%;
  }
}

.code {
  margin: auto;
  width: 50%;
  padding: 40px;
  border-radius: 9px;
  background: #777;
  letter-spacing: 2px;
  text-align: left;
}

.finish {
  margin-top: 300px;
  font-size: 3em;
}

.but button {
  margin: 50px 10px;
}